export default "import { Component } from '@angular/core';\r\nimport { HttpClient } from '@angular/common/http';\r\n\r\nimport { noop, Observable, Observer, of } from 'rxjs';\r\nimport { map, switchMap, tap } from 'rxjs/operators';\r\n\r\ninterface GitHubUserSearchResponse {\r\n  total_count: number;\r\n  incomplete_results: boolean;\r\n  items: GitHubUser[];\r\n}\r\n\r\ninterface GitHubUser {\r\n  login: string;\r\n  id:  number;\r\n  node_id: string;\r\n  avatar_url: string;\r\n  gravatar_id: string;\r\n  url: string;\r\n  html_url: string;\r\n  followers_url: string;\r\n  subscriptions_url: string;\r\n  organizations_url: string;\r\n  repos_url: string;\r\n  received_events_url: string;\r\n  type: string;\r\n  score: number;\r\n}\r\n\r\n@Component({\r\n  selector: 'demo-typeahead-async-http',\r\n  templateUrl: './async-http-request.html'\r\n})\r\nexport class DemoTypeaheadAsyncHttpRequestComponent {\r\n  search: string;\r\n  suggestions$: Observable<GitHubUser[]>;\r\n  errorMessage: string;\r\n\r\n  constructor(private http: HttpClient) {}\r\n\r\n  ngOnInit(): void {\r\n    this.suggestions$ = new Observable((observer: Observer<string>) => {\r\n      observer.next(this.search);\r\n    }).pipe(\r\n      switchMap((query: string) => {\r\n        if (query) {\r\n          // using github public api to get users by name\r\n          return this.http.get<GitHubUserSearchResponse>(\r\n            'https://api.github.com/search/users', {\r\n            params: { q: query }\r\n          }).pipe(\r\n            map((data: GitHubUserSearchResponse) => data && data.items || []),\r\n            tap(() => noop, err => {\r\n              // in case of http error\r\n              this.errorMessage = err && err.message || 'Something goes wrong';\r\n            })\r\n          );\r\n        }\r\n\r\n        return of([]);\r\n      })\r\n    );\r\n  }\r\n}\r\n";