export default "import { Component, OnInit } from '@angular/core';\n\nimport { TypeaheadOrder } from 'bao-tran-angular/typeahead';\nimport { Observable, of, Subscriber } from 'rxjs';\nimport { switchMap } from 'rxjs/operators';\n\n@Component({\n  selector: 'demo-typeahead-ordering',\n  templateUrl: './ordering.html'\n})\nexport class DemoTypeaheadOrderingComponent implements OnInit {\n  selected1: string;\n  selected2: string;\n  selected3: string;\n  selected4: string;\n  sortConfig1: TypeaheadOrder = {\n    direction: 'desc'\n  };\n  sortConfig2: TypeaheadOrder = {\n    direction: 'asc'\n  };\n  sortConfig3: TypeaheadOrder = {\n    direction: 'asc',\n    field: 'city'\n  };\n  states$: Observable<string[]>;\n  states: string[] = [\n    'New Mexico',\n    'New York',\n    'North Dakota',\n    'North Carolina',\n    'Ohio',\n    'Oklahoma',\n    'Oregon',\n    'Pennsylvania',\n    'Rhode Island',\n    'South Carolina',\n    'South Dakota',\n    'Tennessee',\n    'Texas',\n    'Utah',\n    'Alaska',\n    'Alabama',\n    'Iowa',\n    'Kansas',\n    'Kentucky',\n    'Louisiana',\n    'Maine',\n    'Maryland',\n    'Massachusetts',\n    'Michigan',\n    'Minnesota',\n    'Mississippi',\n    'Missouri',\n    'Montana',\n    'Nebraska',\n    'Nevada',\n    'New Hampshire',\n    'New Jersey',\n    'Arizona',\n    'Arkansas',\n    'California',\n    'Colorado',\n    'Connecticut',\n    'Delaware',\n    'Florida',\n    'Georgia',\n    'Hawaii',\n    'Idaho',\n    'Illinois',\n    'Indiana',\n    'Vermont',\n    'Virginia',\n    'Washington',\n    'West Virginia',\n    'Wisconsin',\n    'Wyoming'\n  ];\n  cities = [{\n    city: 'Norton',\n    state: 'Virginia',\n    code: '61523'\n  }, {\n    city: 'Grundy',\n    state: 'Virginia',\n    code: '77054'\n  }, {\n    city: 'Coeburn',\n    state: 'Virginia',\n    code: '01665'\n  }, {\n    city: 'Phoenix',\n    state: 'Arizona',\n    code: '29128'\n  }, {\n    city: 'Tucson',\n    state: 'Arizona',\n    code: '32084'\n  }, {\n    city: 'Mesa',\n    state: 'Arizona',\n    code: '21465'\n  }, {\n    city: 'Independence',\n    state: 'Missouri',\n    code: '26887'\n  }, {\n    city: 'Kansas City',\n    state: 'Missouri',\n    code: '79286'\n  }, {\n    city: 'Springfield',\n    state: 'Missouri',\n    code: '92325'\n  }, {\n    city: 'St. Louis',\n    state: 'Missouri',\n    code: '64891'\n  }];\n\n  ngOnInit(): void {\n    this.states$ = new Observable((observer: Subscriber<string>) => {\n      // Runs on every search\n      observer.next(this.selected4);\n    })\n      .pipe(\n        switchMap((token: string) => {\n          const query = new RegExp(token, 'i');\n\n          return of(\n            this.states.filter((state: string) => query.test(state))\n          );\n        })\n      );\n  }\n}\n";